#work-list-container {
    width: 100%;
    margin: 0;
    padding: 0;
    opacity: 1;
    -webkit-transition: opacity 0.2s;
    -o-transition: opacity 0.2s;
    transition: opacity 0.2s;
}

#work-list {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: row wrap;
        flex-flow: row wrap;
    -ms-flex-pack: center;
        justify-content: center;
    margin: 18px;
    padding: 0;
}

.empty-cells {
    content: '';
    margin: 0 9px;
    width: 360px;
    height: 0;
}



